import React from "react";

// Wheel color changing renders
class WheelColor extends React.Component {
  render() {
    const { active } = this.props;
    return (
      <div className="music">
        <h2>Wheel Color</h2>
        <ul>
          {["Black", "White", "Brown", "Purple"].map((element, index) => {
            return active === index ? (
              <li key={index} className="active theme-li">
                &nbsp;&raquo;&nbsp;{element}
              </li>
            ) : (
              <li className="theme-li" key={index}>
                {element}{" "}
              </li>
            );
          })}
        </ul>
      </div>
    );
  }
}

export default WheelColor;
